.skill {
   
    height: 100vh;
    max-width: 75rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1vw 4vw;
}

.skill h1 {
    font-size: 2.2rem;
    padding: 0.8rem 0;
}

.skill p {
    text-align: center;
    font-size: 0.9rem;
    color: rgb(63, 62, 62);

}

.skill .skillBar {
    margin: 0 auto;
}

.skill .skillBar .development {
    background-color: #dcdada;
    display: flex;
    margin: 1rem 0;
 flex-wrap: wrap;
    align-items: center;
    padding: 0.3rem 1.2rem;
    border-radius: 2rem;
   
}

.skill .skillBar .development .img {
    height: 5.2rem;
    text-align: center;
}

.skill .skillBar .development .coontet {
    flex-direction: column;
    padding: 0 0.5rem;
}

.skill .skillBar .development .coontet span {
    font-size: 1.3rem;
}

.skill .skillBar .development .coontet p {
    font-size: 0.8rem;
    color: rgb(133, 131, 131);
}
@media (max-width:400px) {
    .skill .skillBar .development{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    .skill{
        margin: 5rem 0;
    }
    
}
@media (max-width:768px) {
    .skill{
        width: 98.5%;
        margin: 0 auto;
    }
    .skill .skillBar .development{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}