.work {
    width: 75%;
    margin: 0 auto;
    padding: 2vw 6vw;

}

.work>h1 {
    font-size: 2rem;
    text-align: center;
    padding: 0.4rem 0;
}

.work>p {
    font-size: 1.1rem;
    color: gray;
    text-align: center;
    padding: 0.3rem 0;
}

.imgItem {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 2vw;
    padding: 2rem 0;
   
}

.imgItem .img {
    width: 12rem;
    height: 16rem;
    box-shadow: 0 0 0.3rem 0.1rem gray;
    padding: 0.1rem;
    object-fit: cover;
 border-radius:0.8  rem;
 overflow: hidden;
}

.imgItem .img img {
    width: 100%;
    height: 100%;
    object-fit:cover;
   filter: grayscale(0.6);
}
.imgItem .img:hover img{
    transform: scale(1.1);
    overflow: hidden;
   filter: grayscale(0);

}
.button{
    padding: 0.3rem 0.6rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.button button{
    padding: 0.3rem 0.8rem;
    font-size: 1.2rem;
    border: none;
    border-radius: 100px;
    background-color: rgb(88, 201, 239);
    text-transform: capitalize;
    font-weight: 600;

}
.button button:hover{
    color: #333;
    background-color: rgb(92, 143, 214);
}
@media (max-width:400px) {
    .work{
        margin-top: 5rem;
    }
}