.resource {
    width: 75%;
    margin: 2rem auto;

}

.resource>h1 {
    text-align: center;
    font-size: 2rem;
    padding: 0.2rem 0;
}

.resource>h2 {
    text-align: center;
    font-size: 2rem;
    padding: 0.2rem 0;
    margin: 1.2rem 0;
}

.resource>p {
    font-size: 1.1rem;
    color: gray;
    text-align: center;
}



.companies {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 2vw 3vw;
    gap: 1.2rem;
}

.companies .img {
    width: 15rem;
    height: 4rem;
    padding: 0.1rem 0.2rem;
    box-shadow: 0 0 0.3rem 0.1rem grey;

}

.companies .img img {
    width: 14rem;
    height: 3.6rem;
    object-fit: cover;
}

form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 1.2rem 0;
}

form .name {
    width: 40%;
    height: 2.3rem;
    font-size: 1.1rem;
    padding: 0.1rem 0.8rem;
    border-radius: 10px;
    border: none;
    background-color: #ddd;
}

form .email {
    width: 40%;
    height: 2.3rem;
    font-size: 1.1rem;
    padding: 0.1rem 0.8rem;
    border-radius: 10px;
    border: none;
    background-color: #ddd;
}

form .area {
    width: 40%;
    border-radius: 1.2rem;
    font-size: 1.1rem;
    padding: 0.2rem 0.8rem;

    border: none;
    background-color: #ddd;

}
form .button{
    display: flex;
    align-items: center;
    justify-self:center;
    font-size: 1.2rem;
    margin-bottom: 2rem;
    padding: 0.4rem 1rem;
    border: none;
    border-radius: 10px;
    background-color: black;
    color: aliceblue;
}
.icons{
    display: flex;
    align-items: center;
    justify-content:center;
    gap: 2rem;
    padding: 1.2rem 0;
}
.icons i:nth-child(4){
    font-size:2.3rem;
    color:#b30c0c;
}
.icons i:nth-child(3){
    font-size:2.3rem;
    color:#f71010;
}
.icons i:nth-child(2){
    font-size:2.3rem;
    color:#248ab9;
}
.icons i:nth-child(1){
    font-size:2.3rem;
    color:#6530e1;

}
