.navbar{
    height:4rem;
   
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5vw;
    position: sticky;
    top: 0;
    margin-bottom: 3rem;
    z-index: 1000;
    background-color:#f1f1f1;
    
}
.navbar .navbar-content{
    display: flex;
    align-items: center;
    gap: 3vw;
}
.navbar .navbar-content .navbarMenu{
    font-size: 1.2rem;
    font-weight:500;
}
.navbar .navbar-content .navbarMenu:hover{
    color: aqua;
    margin-bottom: 0.2rem;
    border-bottom: 0.2rem solid yellow;
    transition: all 0.2s ease-in;
    transform: skew(20deg);
}
.navbar img{
    height:3.8rem;
    width: 200px;
}
.navbar .btn{
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    padding: 0.2rem 0.8rem;
    background-color: black;
    border: none;
    color: aliceblue;
   border-radius: 100px;
   gap: 0.3rem;
   font-weight: 100;
   letter-spacing: -1px;
   white-space: nowrap;
}
.navbar .btn:hover{
    background-color: #ddd;
    color:black;
    font-weight: 500;
    transition: 0.2s;
    transform: skew(10deg);
}
.active{
    color: aqua;
    margin-bottom: 0.2rem;
    border-bottom: 0.2rem solid yellow;
    transition: all 0.2s ease-in;
    transform: skew(20deg);
}
.navbar span{
    display: none;
    font-size: 2rem;
    font-weight: 600;
    color: goldenrod;
}

@media (max-width:768px) {
    .navbar .navbar-content{
        display: none;
    } 
    .navbar .btn{
        display: none;
    }
    .navbar{
        width: 98.5%;
        margin: 0 auto;
    }
    .navbar span{
        display: block;
    }
    .slide-content{
        height: 15rem;
        width: 10rem;
        position: absolute;
       top:0.2rem;
        background-color: rgb(91, 92, 92);
        right: 0;
        border-radius: 5px;
        gap: 1.2rem;
        padding: 2rem 0.5rem;
    }
   
    
}