.intro {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    max-width: 75rem;
    height: calc(100vh-4rem);
    margin: 0 auto;
    overflow: hidden;
}

.intro img {
    position: absolute;
    top: -4rem;
    z-index: -1;
    right: 0;
    height: 100vh;
    object-fit: cover;
}

.intro .intro-content {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    font-weight: 600;
}

.intro .intro-content h5 {
    font-size: 1.2rem;
    font-weight: 600;
}

.intro .intro-content h1 {
    font-size: 2.6rem;
}

.intro .intro-content h1 span {
    color: yellow;
    font-size: 2.7rem;
}

.intro .intro-content p {
    color: gray;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    padding: 0.3rem 0;
    font-size: 1.1rem;
}

.intro .intro-content button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 8rem;
    height: 3rem;
    font-size: 1.2rem;
    white-space: nowrap;
    background-color: black;
    color: aliceblue;
    border-radius: 100px;
    gap: 0.6rem;
    padding: 0.3rem 0.6rem;
}

.intro .intro-content button:hover {
    animation: ani 0.3s linear infinite;
}

@keyframes ani {
    0% {
        transform: translate(0rem);
    }

    50% {
        transform: translate(-.235rem);
    }

    100% {
        transform: translate(.235rem);
    }
}

@media screen and (max-width:768px) {
    .intro{
        width: 98.5%;
       margin-bottom: 2rem;
    }
    .intro img {
        right: -15vw;
    }

    .intro .intro-content h5 {
        font-size: 2.5vw;
        color: rgb(124, 125, 126);

    }

    .intro .intro-content h1 {
        font-size: 6.2vw;
        color: rgb(85, 85, 85);

    }

    .intro .intro-content h1 span {
        font-size: 6.4vw;
    }

    .intro .intro-content p {
        font-size: 2.6vw;
    }

    .intro .intro-content button{
        display: flex;
        align-items: center;
        justify-content: center;
       width: 20vw;
       height:6vh;
        font-size:2.5vw;
        white-space: nowrap;
        background-color: black;
        color: aliceblue;
        border-radius: 100px;
        gap:1vw;
        padding: 0.2vw 0.9vw;
    }
}